* {
	box-sizing: border-box;
}

#ain{
	height: 500px;
	width: 500px;
	border: 1px solid black;
/*	margin: 20px;*/
	/*right: 200px;*/
	/*bottom: 200px;*/
	top: 200px;
	left: 200px;
	position: relative;
	padding: 30px;
	text-align: center;
}

h1 {
	transform: translate(0px, -10px);
	font-family: "Helvetica";
	border: 2px dashed red;
	text-align: center;
	margin-bottom: 4px;
}	

#justGray {
	background-color: lightgray;
}
.store-selector {
	width: 100%;
}
.store-selector input,
.store-selector button {
  width: 100%;
  height: 20%;
}
.store-selector input[type="text"],
.store-selector input[type="textarea"],
.store-selector button[type="text"] {
  text-align: center;
  font-size: 1rem;
}
.catch-of-the-day {
	position: relative;
}
.stack {
	width: 350px;
	height: 90vh;
	border: 5px double gray;
	padding: 15px;

	/*float: left;*/
}
.fishpix {
	height: 10px;
	width: 10px;
}
.menu {
	position: relative;
}
.horiLine {
	border-bottom: 2px solid blue;
	height: 200px;
	position: absolute;
}
.listOfFishes {
	position: absolute;
	top: 150px;
	left: 10px;
	z-index: 10;
}
@media (min-width: 400px) {
	.stack {
		width: 50%;
		float: left;
	}
}

@media (min-width: 600px) {
	.stack {
		width: 33.333%;
		float: left;
	}
}
@media (min-width: 800px) {
	.stack {
		width: 25%;
		float: left;
	}
}